import { head } from "./fable_modules/fable-library.3.4.0/Array.js";
import { toText, printf, toConsoleError, split } from "./fable_modules/fable-library.3.4.0/String.js";
import { Options } from "./OidcClient.fs.js";
import { UserManager } from "oidc-client";

const options = (() => {
    const url = head(split(window.location.href, ["?", "#"]));
    return new Options("https://fablecriipto-test.criipto.id", "urn:my:application:identifier:1744", url, "code", url, "urn:grn:authn:dk:mitid:low");
})();

const userManager = new UserManager(options);

export const isAuthenticated = (() => {
    const href = window.location.href;
    return (href.indexOf("?") >= 0) ? (href.indexOf("code=") >= 0) : false;
})();

export function logOut() {
    try {
        userManager.signoutRedirect();
    }
    catch (e) {
        const arg10 = e.message;
        toConsoleError(printf("Eror when logging out: %s"))(arg10);
    }
}

export function logIn() {
    return userManager.signinRedirect();
}

export function registerLogin(setUser) {
    return userManager.processSigninResponse().then((userInfo) => {
        const json = JSON.stringify(userInfo);
        localStorage.setItem(toText(printf("oidc.user:%s:%s"))(options.authority)(options.client_id), toText(printf("%A"))(json));
        setUser(userInfo);
    }).catch((err) => {
        console.error("Error:", err);
        setUser(void 0);
    });
}

